.news-block {
  background: #fff;
  border-top: 3px solid #F79B12;

  & > div {
    padding: 0;
  }

  .news-list {
    padding: 0;
    margin: 0;
    list-style: none;

    li {
      padding: 15px 0;
      height: 119px;
      background: rgb(245,245,245);
      border: 1px solid rgb(236, 238, 243);

      .list-img {
        float: left;
        width: 140px;
        height: 80px;
      }

      .list-content {
        float: left;
      }

      img {
        height: 80px;
      }

      .title {
        font-size: 16px;
        min-height: 40px;
        background: transparent;
      }

      .content {
        font-size: 14px;
        background: transparent;
      }

      &:hover {
        background: #fff;
      }
    }
  }

  .new-info {
    padding: 30px 0 0;
    position: relative;
    height: 476px;

    .title {
      font-size: 28px;
      text-align: center;
    }

    .thum {
      overflow: hidden;
      width: 100%;
      height: 220px;
      position: absolute;
      bottom: 0;

      img {
        width: 100%;
      }
    }

    .content {
      font-size: 16px;
      padding: 0 110px;
    }
  }
}
.news-page.news-block{
    .news-list{
      li{
        background: #fff;
        border-width: 0 0 1px 0;
        border-bottom: 1px solid rgb(236, 238, 243);

        &:hover {
          background: rgb(245,245,245);
        }
        a{
          color: #333;
          &:hover {
          color: #F79B12
          }
        }
      }
    }
}
.jumbotron p {
  color: #fff;
  &.head-title {
    font-size: 36px;
    line-height: 28px;
    padding: 0 18px;
    font-weight: bold;
    line-height: 45px;
    border-left: 5px solid #F79B12;
  }
}
.barlink{
  height: 40px;
  line-height: 40px;
      font-size: 16px;
  text-align: center;
  &:hover{
    background: #F79B12;
    a{
    color: #fff;
    }


  }
}
.breadcrumb {
  margin: 40px 0 30px;
  background: transparent;
}

.news-content {
  padding: 20px 40px;
  font: 18px/32px "Microsoft Yahei";
  color: #404040;
  text-indent: 40px;
}

.next-post {
  background: #f4f4f4;
  padding: 80px 0;
  font-size: 14px;

  .tips {
    font-size: 18px;
  }

  .next-title {
    font-size: 36px;
  }

  .next-contet {
    font-size: 18px;
    color: #888;
  }
}

.pagination>li>a:focus, .pagination>li>a:hover, .pagination>li>span:focus, .pagination>li>span:hover{
  color: #F79B12;
}
.pagination>.active>a, .pagination>.active>a:focus, .pagination>.active>a:hover, .pagination>.active>span, .pagination>.active>span:focus, .pagination>.active>span:hover{
  background-color: #F79B12;
  border-color: #F79B12;
}
.pagination>li>a, .pagination>li>span{
  color: #F79B12;
}
.pagination{
  float: right;
}